<template>
  <div>
    <van-search
      v-model="searchValue"
      placeholder="请输入搜索关键词"
      shape="round"
    />

    <!-- 热门搜索 -->
    <div class="search_wrap" v-if="resultList.length === 0">
      <p class="hot_title">热门搜索</p>
      <!-- 热门搜索的关键字 -->
      <ul class="hot_name_wrap">
        <li
          class="hot_item"
          v-for="(obj, index) in hotList"
          :key="index"
          @click="btn(obj.first)"
        >
          {{ obj.first }}
        </li>
      </ul>
    </div>

    <!-- 搜索结果 -->
    <div class="search_wrap" v-else>
      <p class="hot_title">最佳匹配</p>

      <SongItem
        v-for="obj in resultList"
        :key="obj.id"
        :name="obj.name"
        :author="obj.ar[0].name"
        :id="obj.id"
      ></SongItem>
    </div>
  </div>
</template>


<script>
import { hotSearchAPI, searchResultAPI } from "@/api";
import SongItem from "@/components/SongItem";
export default {
  data() {
    return {
      searchValue: "",
      hotList: [], //热搜关键字
      resultList: [], //搜索结果
      timer: null, //保存定时器
    };
  },
  async created() {
    const res = await hotSearchAPI();
    // console.log(res);
    this.hotList = res.data.result.hots;
  },
  methods: {
    async btn(str) {
      this.searchValue = str; //点击的关键字赋值给搜索框
      const res = await searchResultAPI({
        type: 1,
        keywords: this.searchValue,
      });
      // console.log(res);
      this.resultList = res.data.result.songs;
      setTimeout(() => {
        clearTimeout(this.timer);
      }); //防止下面的请求再次执行
    },
  },
  watch: {
    searchValue(val) {
      //搜索框里的值改变（点击、键盘输入），立即执行
      clearTimeout(this.timer);
      // 防止输入框空值，并且返回数据
      if (val.length === 0) return (this.resultList = []);
      //判断输入框没有值
      this.timer = setTimeout(async () => {
        const res = await searchResultAPI({
          type: 1,
          keywords: val,
        });
        // console.log(res);
        this.resultList = res.data.result.songs;
      }, 1000);
    },
  },
  // 防抖：n秒内，最后执行一次 （函数在n秒内在次执行，那我们重新计时）
  // 节流：n秒后执行一次
  components: {
    SongItem,
  },
};
</script>

<style scoped>
::v-/deep/ .van-field__contrik {
  font-size: 10px !important;
}

/* 搜索容器的样式 */
.search_wrap {
  padding: 0.266667rem;
}

/* 热门搜索文字标题样式 */
.hot_title {
  font-size: 0.32rem;
  color: #666;
}

/* 热门搜索的关键字 */
.hot_name_wrap {
  margin: 00.266667rem 0;
}
.hot_item {
  display: inline-block;
  height: 0.853333rem;
  margin-right: 0.213333rem;
  margin-bottom: 0.213333rem;
  padding: 0 0.373333rem;
  font-size: 0.373333rem;
  line-height: 0.853333rem;
  color: #333;
  border-color: #d3d4da;
  border-radius: 00.853333rem;
  border: 1px solid #d3d4da;
}
</style>