import Vue from 'vue'
import App from './App.vue'
// 用相对路径不好
// 原因：文件如果以后改变位置，路径代码也要改变
// 解决：绝对路径，vue脚手架内置的webpack定义了一些路径变量
// "@"路径里@，代表的是src绝对路径   //D:/实习项目/网易云音乐/music-demo/src
import "@/assets/fonts/iconfont.css" //字体样式文件
import "@/mobile/flexible" //自动适应宽度，改变html的font-size
import "@/styles/reset.css" //初始化标签默认的样式
import router from "@/router" // 导入路由对象

// 全局注册vant组件（按需引入）
import vant, { NavBar, Tabbar, TabbarItem, Col, Row, Image as VanImage, Cell, CellGroup, Icon, Search } from 'vant';
import 'vant/lib/index.css';

Vue.use(NavBar);
Vue.use(TabbarItem);
Vue.use(Tabbar);
Vue.use(Col);
Vue.use(Row);
Vue.use(VanImage); //Image可能跟HTML5内置的标签重名
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Icon);
Vue.use(Search)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
